import React from 'react';
import CurrencyInputV2 from './CurrencyInputV2';

export default () => {
  const [currencyInputV2, setCurrencyInputV2] = React.useState({
    formattedValue: '',
    floatValue: 0,
    decimalScale: 2,
    prefix: 'R$ '
  })

  return (
    <div style={{padding: 10}}>
      <h2>New CurrencyInput</h2>
      <div>
        Decimals: <input
          type='number'
          value={currencyInputV2.decimalScale}
          min="0"
          onChange={({ target: { value } }) => {
            setCurrencyInputV2({
              ...currencyInputV2,
              decimalScale: value
            })
          }}
        />
      </div>

      <div>
        Prefix : <input
          value={currencyInputV2.prefix}
          onChange={({ target: { value } }) => {
            setCurrencyInputV2({
              ...currencyInputV2,
              prefix: value
            })
          }}
        />
      </div>

      <div style={{ marginTop: 20 }}>
        Input: <CurrencyInputV2
          value={currencyInputV2.floatValue}
          decimalScale={currencyInputV2.decimalScale}
          prefix={currencyInputV2.prefix}
          onChange={
            ({
              formattedValue,
              floatValue,
            }) => setCurrencyInputV2({
              ...currencyInputV2,
              formattedValue,
              floatValue,
            })
          }
        />
      </div>

      <div>
        <h4 style={{ marginBottom: 0 }}>
          FormattedValue: "{currencyInputV2.formattedValue}"
        </h4>
        <h4 style={{ marginTop: 0 }}>
          FloatValue: {currencyInputV2.floatValue}
        </h4>
      </div>
    </div>
  )
}
