
import React, { useState, useEffect, useCallback } from 'react';

const composeFunctions = (...functions) => value => functions.reduce((result, func) => func(result), value);

const CurrencyFormatter = ({
  decimalScale = 0,
  decimalSeparator = '.',
  thousandSeparator = '',
  prefix = '',
  defaultEmptyValue = 0,
  max = null,
  allowNegative = false,
} = {}) => {
  if (decimalScale !== 0 && thousandSeparator === decimalSeparator) {
    throw new Error(
      `CurrencyFormatter: thousand separator (${thousandSeparator}) can not be equal to decimal separator (${decimalSeparator})`
    );
  }

  const findThousandSeparatorsReg = new RegExp(`[${thousandSeparator}]`, 'g');

  const getMaxSafeValue = () =>
    decimalScale === 0 ? Number.MAX_SAFE_INTEGER : Number.MAX_SAFE_INTEGER / 2 ** (decimalScale * 4 - 1);

  const addPrefix = value => `${prefix}${value}`;

  const removePrefix = value => value.replace(prefix.trim(), '');

  const addThousandSeparatorToIntegerPart = value => value.replace(/\B(?=(\d{3})+(?!\d))/g, thousandSeparator);

  const removeThousandSeparator = value => value.replace(findThousandSeparatorsReg, '');

  const addFloatDecimalSeparator = value => value.replace(decimalSeparator, '.');

  const floatValueToString = value => value.toFixed(decimalScale);

  const removeWhiteSpaces = value => value.replace(/\s/g, '');

  const getNumberWithDecimalScale = value => Number(value.toFixed(decimalScale));

  const joinIntegerAndDecimalParts = (integerPart, decimalPart) =>
    decimalPart ? [integerPart, decimalPart].join(decimalSeparator) : integerPart;

  const splitIntegerAndDecimalParts = value => value.split('.');

  const removeSign = value => (allowNegative ? value : Math.abs(value));

  const parseFloatToCurrencyString = (floatValue = defaultEmptyValue) => {
    if (Number.isNaN(+floatValue)) return undefined;

    const stringValue = floatValueToString(floatValue);

    const [integerPart, decimalPart] = splitIntegerAndDecimalParts(stringValue);

    const stringIntegerNumber = addThousandSeparatorToIntegerPart(integerPart);

    const formattedValue = joinIntegerAndDecimalParts(stringIntegerNumber, decimalPart);

    return addPrefix(formattedValue);
  };

  const parseCurrencyStringToFloatFormatString = value =>
    composeFunctions(removePrefix, removeWhiteSpaces, removeThousandSeparator, addFloatDecimalSeparator)(value);

  const getNewFloatValue = (value, decimalsPartSize) => {
    switch (true) {
      case decimalsPartSize === 0: // before = 10.00, after = 5, scale = 2 => 0.05
        return value / 10 ** decimalScale;
      case decimalsPartSize > decimalScale: // before = 1.44, after = 1.445, scale = 2 => 14.45
        return value * 10;
      case decimalsPartSize < decimalScale: // before = 1.43, after = 1.4, scale = 2 => 0.14
        return value / 10;
      default:
        // before => 13.00, after => 14.00
        return value;
    }
  };

  const getConvertedNumber = stringFloat => {
    const [, decimalPart] = splitIntegerAndDecimalParts(stringFloat);
    const decimalsPartSize = decimalPart ? decimalPart.length : 0;

    return {
      value: getNewFloatValue(Number(stringFloat), decimalsPartSize),
      decimalsPartSize,
    };
  };

  const parseFloatFormatStringToNumber = stringFloat => {
    if (Number.isNaN(+stringFloat)) return {};

    const { value, decimalsPartSize } = getConvertedNumber(stringFloat);

    const floatValue = removeSign(getNumberWithDecimalScale(value));

    if (max != null && floatValue > max) return {};

    const maxSafeValue = getMaxSafeValue();

    return floatValue > maxSafeValue ? {} : { floatValue, decimalsPartSize };
  };

  const parseCurrencyStringToFloat = currencyString => {
    const floatString = parseCurrencyStringToFloatFormatString(currencyString);

    return parseFloatFormatStringToNumber(floatString);
  };

  return {
    parseCurrencyStringToFloat,
    parseFloatToCurrencyString,
  };
};

const CurrencyInputV2 = ({
  value,
  prefix,
  onChange,
  defaultEmptyValue,
  decimalScale,
  decimalSeparator,
  thousandSeparator,
  max,
  allowNegative,
  inputProps,
  children,
}) => {
  if (Number.isNaN(+value)) {
    throw new Error(`CurrencyInput: Value ("${value}") is not a valid number.`);
  }

  const CurrencyParser = CurrencyFormatter({
    prefix,
    decimalScale,
    decimalSeparator,
    thousandSeparator,
    defaultEmptyValue,
    max,
    allowNegative,
  });

  const [currencyInputValues, setCurrencyInputValues] = useState({
    cursor: {
      position: null,
      target: null,
    },
    values: {
      floatValue: value,
      formattedValue: CurrencyParser.parseFloatToCurrencyString(value),
    },
  });

  const setCursorPositionOnElement = ({ position, target } = {}) => {
    if (target && position && target.setSelectionRange) {
      target.setSelectionRange(position, position);
    }
  };

  const getNewCursorPosition = useCallback(
    ({ currentPosition, oldValue, newValue, decimalsPartSize }) => {
      const inputDiffSize = newValue.length - oldValue.length;

      const positionIsValid = position => {
        const valueTyped = newValue[position - 1];

        return (
          (Number.isNaN(+valueTyped) && valueTyped !== thousandSeparator && valueTyped !== decimalSeparator) ||
          valueTyped === ' '
        );
      };

      const getValidPosition = () => {
        const newPosition = newValue.search(/\d/);

        return newPosition !== -1 ? newPosition : currentPosition;
      };

      switch (true) {
        case decimalsPartSize === 0: // When value is erased and a new one is inputed
          return newValue.length;
        case currentPosition >= oldValue.length - 1: // When cursor is on the last position
          return newValue.length;
        case inputDiffSize === 2: // When the mask add the number and a separator symbol
          return currentPosition + 1;
        case inputDiffSize === -2: // When the mask remove the number and a separator symbol
          return currentPosition - 1;
        case positionIsValid(currentPosition): // When cursor is on some letter or white space (probably prefix)
          return getValidPosition();
        default:
          return currentPosition; // Mantain the currentPosition
      }
    },
    [thousandSeparator, decimalSeparator]
  );

  const commitChange = useCallback(
    ({ floatValue, cursorCurrentPosition, decimalsPartSize, target }) => {
      const formattedValue = CurrencyParser.parseFloatToCurrencyString(floatValue);

      if (formattedValue) {
        const cursorPosition = getNewCursorPosition({
          currentPosition: cursorCurrentPosition,
          oldValue: currencyInputValues.values.formattedValue,
          newValue: formattedValue,
          decimalsPartSize,
        });

        const values = {
          formattedValue,
          floatValue,
        };

        setCurrencyInputValues({
          cursor: {
            target,
            position: cursorPosition,
          },
          values,
        });

        if (onChange) onChange(values);
      }
    },
    [CurrencyParser, currencyInputValues.values.formattedValue, getNewCursorPosition, onChange]
  );

  useEffect(() => {
    if (value !== currencyInputValues.values.floatValue) {
      commitChange({ floatValue: value });
    }
  }, [value, commitChange, currencyInputValues]);

  useEffect(() => {
    setCursorPositionOnElement(currencyInputValues.cursor);
  }, [currencyInputValues]);

  const handleValueChange = ({ target }) => {
    const { value } = target;

    const cursorCurrentPosition = target ? target.selectionStart : null;

    const { floatValue, decimalsPartSize } = CurrencyParser.parseCurrencyStringToFloat(value);
    if (floatValue == null) return;

    commitChange({ floatValue, cursorCurrentPosition, decimalsPartSize, target });
  };

  const getHelpersAndState = () => ({
    onChange: handleValueChange,
    values: currencyInputValues.values,
  });

  return <input
    value={currencyInputValues.values.formattedValue}
    onChange={handleValueChange}
  />
};

CurrencyInputV2.defaultProps = {
  value: 0,
  prefix: 'R$ ',
  onChange: null,
  defaultEmptyValue: 0,
  decimalScale: 2,
  decimalSeparator: ',',
  thousandSeparator: '.',
  max: null,
  inputProps: {},
  allowNegative: false,
};

export default CurrencyInputV2;

